import Link from "next/link";
import cases from "@/data/mock_cases.json";
import { CaseSchema } from "@/lib/schema";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";

function badgeVariant(label: string) {
  if (label === "BLOCK") return "destructive";
  if (label === "ESCALATE") return "secondary";
  return "default";
}

export default async function CaseDetailPage({
  params,
}: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await params;

  const found = (cases as any[]).find((c) => c.id === id);
  if (!found) throw new Error("Case not found");

  const c = CaseSchema.parse(found);

  return (
    <div className="p-6 space-y-6 max-w-5xl">
      <div className="flex items-center gap-4">
        <Link className="underline text-sm" href="/cases">
          Back
        </Link>
        <h1 className="text-2xl font-semibold">{c.id}</h1>
        <span className="text-sm text-muted-foreground">{c.status}</span>
      </div>

      <div className="grid gap-6 md:grid-cols-3">
        <div className="md:col-span-2 space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="text-base">Summary</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2 text-sm">
              <div>
                <span className="text-muted-foreground">User:</span> {c.user_display}
              </div>
              <div>
                <span className="text-muted-foreground">User message:</span> {c.user_message}
              </div>
              <div>
                <span className="text-muted-foreground">Tool:</span>{" "}
                <span className="font-mono text-xs">{c.tool_name}</span>
              </div>
              <div className="flex items-center gap-2">
                <span className="text-muted-foreground">Risk:</span>
                <Badge variant={badgeVariant(c.risk_label)}>{c.risk_label}</Badge>
                <span className="text-muted-foreground">({c.risk_score})</span>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-base">Tool args (redacted)</CardTitle>
            </CardHeader>
            <CardContent>
              <pre className="text-xs bg-muted p-3 rounded overflow-auto">
                {JSON.stringify(c.tool_args_redacted, null, 2)}
              </pre>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-base">Rationale</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2 text-sm">
              <div>{c.risk_rationale}</div>
              <div className="text-xs text-muted-foreground">
                Policy refs: {c.policy_refs.join(", ")}
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-base">Audit trail</CardTitle>
            </CardHeader>
            <CardContent>
              <ul className="text-sm space-y-2">
                {c.history.map((h, idx) => (
                  <li key={idx} className="flex gap-3">
                    <span className="text-xs text-muted-foreground w-48">{h.ts}</span>
                    <span className="text-xs font-mono w-24">{h.actor}</span>
                    <span className="text-xs font-mono w-24">{h.event}</span>
                    <span className="text-sm">{h.detail}</span>
                  </li>
                ))}
              </ul>
            </CardContent>
          </Card>
        </div>

        <div className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="text-base">Decision</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="text-sm text-muted-foreground">
                Approve executes the tool call. Reject blocks it. Request info pauses review.
              </div>
              <Separator />
              <div className="flex flex-col gap-2">
                <Button>Approve</Button>
                <Button variant="destructive">Reject</Button>
                <Button variant="secondary">Request info</Button>
              </div>
              <div className="text-xs text-muted-foreground">
                MVP: these buttons will POST to /api/cases/[id]/decision later.
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
